import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			mobile: '',
			password: '',
			logining: false,
			codetext:'获取验证码',
			codenum:60,
			disabled:false
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toRegist() {
			uni.navigateTo({
				url:"register"
			})
		},
		// 发送验证码
		sendcode(){
			let that = this;
			if(this.codenum ==0){
				this.codetext = "获取验证码"
				this.codenum = 60
				this.disabled = false
				return false
			}else{
				this.disabled = true
				this.codetext = this.codenum+"秒后重新发送"
				this.codenum --
			}
			  setTimeout(()=>{
				  that.sendcode()
			  },1000);
		},
		async Login() {
			uni.navigateTo({
				url:"login"
			})
			return
			this.logining = true;
			const {
				mobile,
				password
			} = this;

			/* 数据验证模块
			if(!this.$api.match({
				mobile,
				password
			})){
				this.logining = false;
				return;
			}
			*/
			const sendData = {
				mobile,
				password
			};
			const result = await this.$api.json('userInfo');
			if (result.status === 1) {
				this.login(result.data);
				uni.navigateBack();
			} else {
				this.$api.msg(result.msg);
				this.logining = false;
			}
		}
	},

}
